@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@100;200;300;400;500;600;700&display=swap);
* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    font-family: 'Noto Sans Display', sans-serif;
  }
.container {
    background-color: rgb(117, 194, 246);
    padding: 25px 5px;
    width: 100%;
    margin: 0 auto;
    height: 100px;
}

.wrapp_links {
    margin-top: 20px;
    display: flex;
    flex-direction: column-reverse;
}

.inner_link {
    cursor: pointer;
    border: 1px solid #44aaee;
    width: 310px;
    height: 50px;
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 20px auto;
}

.inner_link:hover {
    background-color: #57b6f5;
}

.inner_link:hover .link{
    color: #fff;
}

.link {
    color: #364955;
    text-decoration: none;
    font-size: 25px;
}

.search-form {
    justify-content: center;
    align-items: center;
    display: flex;
  }

.search-input {
    outline: none;
    width: 300px;
    height: 35px;
    border: none;
    padding: 0;
    border: 1px solid #44aaee;
  }
  
  input[type="text"] {
    padding: 10px;
  }
  
  .search-input:-ms-input-placeholder {
    padding-left: 10px;
  }
  
  .search-input:placeholder-shown {
    padding-left: 10px;
  }
  
  .search-btn {
    position: relative;
    color: #fff;
    background: #44aaee;
    cursor: pointer;
    display: block;
    font-size: 18px;
    font-weight: 400;
    letter-spacing: 0.04em;
    width: 100px;
    height: 35px;
    border: none;
    transition: background .2s;
  }
  
  .search-btn:hover {
    background: #5db7f3;
    transition: all .3s;
  }
.wrapp_info {
    max-width: 1000px;
    margin: 0 auto;
    padding: 0 10px;
}
.wrapp_content {
    text-align: center;
    padding: 10px;
    margin: 20px 0px;
    display: block;
    background-color: rgb(117, 194, 246);
    text-decoration: none;
    transition: -webkit-transform .2s;
    transition: transform .2s;
    transition: transform .2s, -webkit-transform .2s;
}

.wrapp_content:hover {
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
    transition: -webkit-transform .2s;
    transition: transform .2s;
    transition: transform .2s, -webkit-transform .2s;
}

h2 {
    line-height: 50px;
    font-size: 30px;
    font-weight: 700;
    letter-spacing: .03em;
}

h3 {
    font-size: 20px;
    font-weight: 500;
    letter-spacing: .03em;
}

h2, h3 {
    color: #212121;
    font-family: 'Montserrat', sans-serif;
}
.info_container {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    max-width: 1000px;
    margin: 0 auto;
    padding: 0 10px;
    height: 100vh;
}

.title_NameVar {
    font-size: 35px;
    font-weight: 600;
    letter-spacing: .02em;
    margin-bottom: 15px;
}

.description {
    font-weight: 400;
    font-size: 20px;
    letter-spacing: 0.03em;
}

.home_link {
    margin-top: 20px;
    display: flex;
    justify-content: center;
    align-items: center;
    text-decoration: none;
    font-size: 20px;
    color: #fff;
    width: 150px;
    height: 40px;
    background-color: rgb(117, 194, 246);
}
